body{
    display:flex;
    align-items:center;
    justify-content: center;
    margin:0px;
    background-color: aquamarine;
}
/** img div style**/
.img{
    width:50px;
    height:50px;
    border-radius: 20px;
}
/** main where all info is div style**/
.maindiv_1{
    display:flex;
    align-items:center;
    justify-content: space-around;
    border-radius:10px;
    border: 2px solid green;
}

/** where bitcoin name is**/
.namediv{
    width:400px;
    height:70px;
    background-color: rgb(23, 189, 12);
    border-radius: 10px;
    font-family: 'Courier New', Courier, monospace;
    font-size:30px;
    color:white;
    text-align:center;
    cursor:pointer;
    
}
/** where rank info is**/
.allinfo{
    width:200px;
    height:40px;
    border:2px solid rgb(91, 89, 89);
    color:rgb(2, 0, 124);
    font-family: 'Courier New', Courier, monospace;
    font-size:20px;
    border-radius:20px;
    text-align: center;
    cursor:pointer;
    font-weight: bold;
}
.allinfo:hover{
    background-color: white;
}
/** img back div style**/
.imgback{
    display:flex;
    align-items: center;
    justify-content: center;
    width:100px;
    height:100px;
    background-color: white;
    cursor:pointer;
    border-radius:50px;
}
.imgback:hover{
background-color: black;
}
/****/
.up{
    width:50px;
    height:20px;
    background-color: blue;
    border-radius:20px;
    text-align:center;
    cursor:pointer;
    color:white;

}
.down{
    width:50px;
    height:20px;
    background-color: blue;
    border-radius:20px;
    text-align:center;
    cursor:pointer;
    color:white;
}
.price_div{
display:flex;
}
.price_result{
    width:300px;
    height:100px;
    background-color: black;
    text-align: center;
    color:white;
    font-size:30px;
    border-radius:20px;
    border:3px solid white;
}
.h1counter{
    color:red;
}


.statistics{
    width: 40px;
    height:40px;
    background-color: red;
    border-radius:30px;
   text-align: center;
   font-size: 40px;
   cursor:pointer;
   color:green;
   font-weight: 400;
}










